import { useTheme } from '@emotion/react';
import { useState, useEffect } from 'react';
import { LoadingButton } from '@mui/lab';
import {
  Card, CardHeader, CardContent,
  Button,
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableRow,
  Divider,
  InputLabel, Select, MenuItem,
  Grid, TextField, Radio, FormControl,
  Autocomplete
}
  from '@mui/material';
import { TextareaAutosize } from '@mui/base';
import { Box, styled } from '@mui/system';
import { Paragraph } from 'app/components/Typography';
import useAuth from 'app/hooks/useAuth';
import axios from 'axios';
import { Formik, FieldArray } from 'formik';
import { NavLink, useNavigate, Link } from 'react-router-dom';
import * as Yup from 'yup';
import swal from 'sweetalert';
import { Breadcrumb } from "app/components";

const FlexBox = styled(Box)(() => ({ display: 'flex', alignItems: 'center' }));

const JustifyBox = styled(FlexBox)(() => ({ justifyContent: 'center' }));

const ContentBox = styled(JustifyBox)(() => ({
  height: '100%',
  padding: '32px',
  background: 'rgba(0, 0, 0, 0.01)',
}));

const Container = styled("div")(({ theme }) => (
  {
    margin: "30px",
    [theme.breakpoints.down("sm")]: { margin: "16px" },
    "& .breadcrumb": {
      marginBottom: "30px",
      [theme.breakpoints.down("sm")]: { marginBottom: "16px" },
    },
  }
));

const TaxCategoryTable = styled(Table)(() => ({
  minWidth: 400,
  whiteSpace: 'pre',
  '& small': {
    width: 50,
    height: 15,
    borderRadius: 500,
    boxShadow: '0 0 2px 0 rgba(0, 0, 0, 0.12), 0 2px 2px 0 rgba(0, 0, 0, 0.24)',
  },
  '& td': { borderBottom: 'none' },
  '& td:first-of-type': { paddingLeft: '16px !important' },
}));
// inital login credentials
const initialValues = {
  product_name: '',
  description: '',
  category_id: '',
  brand_name: '',
  unit_packing: '',
  expiry_date: '',
  available_qty: '',
  hsn_sac_code: '',
  batch_no: '',
  mrp: '',
  product_image: '',
  min_qty: '',
  min_qty_discount: '',
  min_qty_bonus_deal: '',
  min_qty_trade_rate: '',
  min_qty_net_rate: '',
  madicine_tax_class: '',
  min_qty_margin_percentage: '',
  status: 'pending',
  tax_category_id: ''
};

// form field validation schema
const validationSchema = Yup.object().shape({
  product_name: Yup.string()
    .min(2, 'Product Name must be 10 digit length')
    .required('Product Name is required!'),
  category_id: Yup.string()
    .required('Category is required!'),
  brand_name: Yup.string()
    .required('Brand is required!'),
  expiry_date: Yup.string()
    .required('Expiry Date is required!'),
  available_qty: Yup.number()
    .required('Available Quantity is required!'),
  hsn_sac_code: Yup.string()
    .required('HSN/SAC is required!'),
  mrp: Yup.number()
    .required('MRP is required!'),
  min_qty: Yup.number()
    .required('Quantity is required!'),
  min_qty_discount: Yup.number()
    .required('Discount (%) is required!'),
  min_qty_bonus_deal: Yup.string()
    .required('Bonus (Deal) is required!'),
  min_qty_trade_rate: Yup.number()
    .required('Trade Rate is required!'),
  tax_category_id: Yup.number()
    .required('Tax Category is required!'),

});

const AddMedicinesStock = () => {
  const theme = useTheme();
  const { register } = useAuth();
  const navigate = useNavigate();
  const [loading, setLoading] = useState(false);
  const [categories, setCategory] = useState(false);
  const [brands, setBrands] = useState(false);
  const [tax_categories, setTaxCategories] = useState(false);
  const [formData, setFormData] = useState(initialValues);

  useEffect(() => {
    fetchData();
  }, []);

  /**
  * Fetching data from server.
  */
  const fetchData = async () => {
    setLoading(true);
    try {
      await axios.get('/sanctum/csrf-cookie').then(response => {
        axios.post('/api/client/get-brands')
          .then(response => {
            if (response.data.response_code === 200) {
              setBrands(response.data.data);
            } else if (response.data.response_code === 401) {

            }
            setLoading(false);
          })
          .catch(function (error) {
            setLoading(false);
            console.error(error);
          });

        axios.post('/api/client/get-categories')
          .then(response => {
            if (response.data.response_code === 200) {
              setCategory(response.data.data);
            } else if (response.data.response_code === 401) {

            }
            setLoading(false);
          })
          .catch(function (error) {
            setLoading(false);
            console.error(error);
          });


        axios.post('/api/client/get-tax-categories')
          .then(response => {
            if (response.data.response_code === 200) {
              setTaxCategories(response.data.data);
            } else if (response.data.response_code === 401) {

            }
            setLoading(false);
          })
          .catch(function (error) {
            setLoading(false);
            console.error(error);
          });
      });
    } catch (e) {
      setLoading(false);
    }
  }

  const calculateNetRate = (e, values) => {
    // console.log(e.target.value);
    // console.log(e.target.name);
    var min_qty = 0;
    var min_qty_discount = 0;
    var min_qty_bonus_deal = '';
    var min_qty_trade_rate = 0;
    var min_qty_net_rate = 0;
    var igst = 0;
    var cess = 0;
    var tax_category_id = '';
    switch (e.target.name) {
      case 'min_qty':
        min_qty = parseInt(e.target.value);
        min_qty_discount = (values.min_qty_discount) ? parseInt(values.min_qty_discount) : 0;
        min_qty_bonus_deal = (values.min_qty_bonus_deal) ? values.min_qty_bonus_deal : 0;
        min_qty_trade_rate = (values.min_qty_trade_ratep) ? parseInt(values.min_qty_trade_rate) : 0;
        break;
      case 'min_qty_discount':
        min_qty_discount = parseInt(e.target.value);
        min_qty = (values.min_qty) ? parseInt(values.min_qty) : 0;
        min_qty_bonus_deal = (values.min_qty_bonus_deal) ? values.min_qty_bonus_deal : 0;
        min_qty_trade_rate = (values.min_qty_trade_rate) ? parseInt(values.min_qty_trade_rate) : 0;
        break;
      case 'min_qty_bonus_deal':
        min_qty_bonus_deal = e.target.value;
        min_qty = (values.min_qty) ? parseInt(values.min_qty) : 0;
        min_qty_discount = (values.min_qty_discount) ? parseInt(values.min_qty_discount) : 0;
        min_qty_trade_rate = (values.min_qty_trade_rate) ? parseInt(values.min_qty_trade_rate) : 0;
        break;
      case 'min_qty_trade_rate':
        min_qty_trade_rate = parseInt(e.target.value);
        min_qty = (values.min_qty) ? parseInt(values.min_qty) : 0;
        min_qty_discount = (values.min_qty_discount) ? parseInt(values.min_qty_discount) : 0;
        min_qty_bonus_deal = values.min_qty_bonus_deal;
        break;
      case 'tax_category_id':
        tax_category_id = parseInt(e.target.value);
        min_qty = (values.min_qty) ? parseInt(values.min_qty) : 0;
        min_qty_discount = (values.min_qty_discount) ? parseInt(values.min_qty_discount) : 0;
        min_qty_bonus_deal = (values.min_qty_bonus_deal) ? values.min_qty_bonus_deal : 0;
        min_qty_trade_rate = (values.min_qty_trade_rate) ? parseInt(values.min_qty_trade_rate) : 0;
        break;

    }

    var amount_without_tax = 0;
    var qty_trade_rate = 0;
    var qty_trade_rate_dis_pre = 0;
    var dealType = '';
    // console.log('min_qty----:' + min_qty + 'min_qty_discount----:' + min_qty_discount + 'min_qty_bonus_deal----:' + min_qty_bonus_deal + 'min_qty_trade_rate----:' + min_qty_trade_rate);
    if (min_qty && min_qty_trade_rate) {
      qty_trade_rate = (min_qty * min_qty_trade_rate);
      // console.log('CCmin_qty_bonus_deal----:' + min_qty_bonus_deal);
      if (min_qty_bonus_deal == '' || min_qty_bonus_deal == 0 || Number.isInteger(min_qty_bonus_deal)) {
        qty_trade_rate_dis_pre = (qty_trade_rate * min_qty_discount) / 100;
        amount_without_tax = (qty_trade_rate - qty_trade_rate_dis_pre);
        // console.log('Aamount_without_tax----:' + amount_without_tax);
      } else {
        if (min_qty_bonus_deal) {
          min_qty_bonus_deal = min_qty_bonus_deal.replace(/[\(\)]/g, "");
          dealType = min_qty_bonus_deal.split('+');
          console.log('dealType----:' + dealType + 'dealType.length' + dealType.length);
          if (dealType.length > 1) {
            // console.log('deal_x----:' + dealType[0] + 'deal_y' + dealType[1]);

            var deal_x = parseInt(dealType[0]);
            var deal_y = parseInt(dealType[1]);
            var deal_pre = (((deal_y) / (deal_x + deal_y)) * 100);

            var deal_pre_amount = (qty_trade_rate * deal_pre) / 100;

            var amount_after_deal_pre = (qty_trade_rate - deal_pre_amount);
            var discount_amount = (amount_after_deal_pre * min_qty_discount) / 100;
            amount_without_tax = (amount_after_deal_pre - discount_amount);
            // console.log('Bamount_without_tax----:' + amount_without_tax);
          } else {
            qty_trade_rate_dis_pre = (qty_trade_rate * min_qty_discount) / 100;
            amount_without_tax = (qty_trade_rate - qty_trade_rate_dis_pre);
            // console.log('Camount_without_tax----:' + amount_without_tax);
          }
        }
      }
    }

    var total_tax_pre = 0;

    if (tax_category_id !== '') {
      igst = parseFloat(tax_categories[tax_category_id]['igst']);
      cess = parseFloat(tax_categories[tax_category_id]['cess']);
      if (igst && cess) {
        total_tax_pre = (igst + cess);
      } else {
        total_tax_pre = igst;
      }
    }


    var tax_amount = (amount_without_tax * total_tax_pre) / 100;
    min_qty_net_rate = amount_without_tax + tax_amount;

    return min_qty_net_rate;
  };

  const changeHandler = (e) => {
    setFormData({ ...formData, [e.target.name]: e.target.files[0] });
  };

  const handleFormSubmit = (values, errors) => {
    setLoading(true);
    try {
      axios.get('/sanctum/csrf-cookie').then(response => {
        const uploadFormData = new FormData();
        for (let iKey in values) {
          if (iKey === 'product_image') {
            uploadFormData.append(iKey, formData[iKey])
          } else {
            if (values[iKey]) {
              uploadFormData.append(iKey, values[iKey])
            } else {
              uploadFormData.append(iKey, '')
            }
          }
        }
        const config = { headers: { "Content-Type": "multipart/form-data" } };
        axios.post('/api/client/add-medicine-stock', uploadFormData, config)
          .then(response => {
            if (response.data.response_code === 200) {
              swal({
                title: "Good job!",
                text: response.data.message,
                icon: "success",
              });
              navigate('/dashboard/medicine/list');
            } else if (response.data.response_code == 401 || response.data.response_code == '401') {
              swal({
                title: "Warning!",
                text: response.data.message,
                icon: "warning",
              });
              errors.setErrors(response.data.errors);
            } else {
              errors.setErrors(response.data.errors);
            }
            setLoading(false);
          })
          .catch(function (error) {
            console.error(error);
          });
      });
    } catch (e) {
      console.log(e);
      setLoading(false);
    }
  };

  return (
    <Container className="add-medicine">
      <Box className="breadcrumb">
        <Breadcrumb routeSegments={[{ name: "dashboard", path: "/dashboard" }, { name: "Add Medicine" }]} />
      </Box>
      <Card className="card" >
        <CardHeader
          title="Add Medicine"
          titleTypographyProps={{
            variant: "h4",
            fontSize: '16px',
            fontWeight: '600',
            textTransform: 'uppercase',
            textAlign: 'center'
          }}
          action={
            <>
              <Button title="Back" component={Link}
                to={`/dashboard/medicine/list`}
                color="inherit" variant="outlined" size="small" sx={{ m: .5 }}>
                Back
              </Button>
            </>
          }
        />
        <Divider />
        <CardContent>

          <Formik
            onSubmit={(values, errors) => {
              console.log(errors);
              handleFormSubmit(values, errors);
            }
            }
            initialValues={initialValues}
            validationSchema={validationSchema}
            enableReinitialize={true}
          >
            {({ values, errors, touched, handleChange, handleBlur, handleSubmit, setFieldValue, field }) =>
            (

              <form onSubmit={handleSubmit}>
                <Grid container style={{ padding: '20px 0px' }} spacing={2}>
                  <Grid item sm={6} xs={12}>
                    <InputLabel required htmlFor="product_name" className='form-label'>
                      Product Name
                    </InputLabel>
                    <TextField
                      fullWidth
                      size="small"
                      type="text"
                      name="product_name"
                      variant="outlined"
                      onBlur={handleBlur}
                      value={values.product_name}
                      onChange={handleChange}
                      helperText={touched.product_name && errors.product_name}
                      error={Boolean(errors.product_name && touched.product_name)}
                      sx={{ mb: 3 }}
                    />
                  </Grid>
                  <Grid item sm={6} xs={12}>
                    <InputLabel required htmlFor="category_id" className='form-label'>
                      Category
                    </InputLabel>
                    <FormControl fullWidth
                      sx={{ mb: 3 }}
                      size="small"
                    >
                      <Select
                        displayEmpty
                        labelId="status-label"
                        id="category_id"
                        name="category_id"
                        value={values.category_id}
                        onChange={handleChange}
                        variant="outlined"
                        onBlur={handleBlur}
                        error={Boolean(errors.category_id && touched.category_id)}
                      >
                        <MenuItem value="" key="sg">Select Category</MenuItem>
                        {categories && categories.map((row) =>
                          <MenuItem value={row.id} key={row.id}>{row.name}</MenuItem>
                        )}
                      </Select>
                    </FormControl>
                  </Grid>
                  <Grid item sm={6} xs={12}>
                    <InputLabel required htmlFor="brand_id" className='form-label'>Brand</InputLabel>
                    <FormControl fullWidth
                      sx={{ mb: 3 }}
                      size="small"
                    >
                      {brands && (
                        <>
                          <Autocomplete
                            freeSolo
                            size="small"
                            id="brand_name"
                            options={brands && brands.map((option) => option.name)}
                            renderInput={(params) =>
                              <TextField
                                onChange={handleChange}
                                onBlur={handleBlur}
                                name="brand_name"
                                variant="outlined"
                                value={values.brand_name}
                                helperText={touched.unit_packing && errors.unit_packing}
                                error={Boolean(errors.brand_name && touched.brand_name)}
                                {...params}
                              />}
                          />
                          {(errors.brand_name)
                            && (touched.brand_name)
                            && <div style={{ color: '#ff3d57' }}>{`${errors.brand_name}`}</div>}
                        </>
                      )
                      }
                    </FormControl>
                    <label htmlFor="unit_packing" className='form-label'>Unit Packing</label>
                    <TextField
                      fullWidth
                      size="small"
                      type="number"
                      name="unit_packing"
                      // label="Name"
                      variant="outlined"
                      onBlur={handleBlur}
                      value={values.unit_packing}
                      onChange={handleChange}
                      helperText={touched.unit_packing && errors.unit_packing}
                      error={Boolean(errors.unit_packing && touched.unit_packing)}
                      sx={{ mb: 3 }}
                    />

                  </Grid>
                  <Grid item sm={6} xs={12} lg={6} >
                    <label htmlFor="description" className='form-label'>Description</label>
                    <br />
                    <TextField
                      fullWidth
                      multiline={true}
                      minRows={6}
                      size="small"
                      type="text"
                      name="description"
                      variant="outlined"
                      onBlur={handleBlur}
                      value={values.description}
                      onChange={handleChange}
                      // helperText={touched.description && errors.description}
                      // error={Boolean(errors.description && touched.description)}
                      sx={{ mb: 3 }}
                    />
                  </Grid>
                  <Grid item sm={6} xs={12}>
                    <InputLabel required htmlFor="expiry_date" className='form-label'>Expiry Date</InputLabel>

                    <TextField
                      fullWidth
                      size="small"
                      type="date"
                      name="expiry_date"
                      variant="outlined"
                      onBlur={handleBlur}
                      value={values.expiry_date}
                      onChange={handleChange}
                      helperText={touched.expiry_date && errors.expiry_date}
                      error={Boolean(errors.expiry_date && touched.expiry_date)}
                      sx={{ mb: 3 }}
                    />
                  </Grid>
                  <Grid item sm={6} xs={12}>
                    <InputLabel required htmlFor="available_qty" className='form-label'>Available Quantity</InputLabel>
                    <TextField
                      fullWidth
                      size="small"
                      type="number"
                      name="available_qty"
                      // label="Name"
                      variant="outlined"
                      onBlur={handleBlur}
                      value={values.available_qty}
                      onChange={handleChange}
                      helperText={touched.available_qty && errors.available_qty}
                      error={Boolean(errors.available_qty && touched.available_qty)}
                      sx={{ mb: 3 }}
                    />
                  </Grid>
                  <Grid item sm={6} xs={12}>
                    <InputLabel required htmlFor="hsn_sac_code" className='form-label'>HSN/SAC Code</InputLabel>
                    <TextField
                      fullWidth
                      size="small"
                      type="text"
                      name="hsn_sac_code"
                      // label="Name"
                      variant="outlined"
                      onBlur={handleBlur}
                      value={values.hsn_sac_code}
                      onChange={handleChange}
                      helperText={touched.hsn_sac_code && errors.hsn_sac_code}
                      error={Boolean(errors.hsn_sac_code && touched.hsn_sac_code)}
                      sx={{ mb: 3 }}
                    />
                  </Grid>
                  <Grid item sm={6} xs={12}>
                    <label htmlFor="batch_no" className='form-label'>Batch No.</label>
                    <TextField
                      fullWidth
                      size="small"
                      type="text"
                      name="batch_no"
                      // label="Name"
                      variant="outlined"
                      onBlur={handleBlur}
                      value={(values.batch_no)}
                      onChange={handleChange}
                      helperText={touched.batch_no && errors.batch_no}
                      error={Boolean(errors.batch_no && touched.batch_no)}
                      sx={{ mb: 3 }}
                    />
                  </Grid>
                  <Grid item sm={6} xs={12}>
                    <InputLabel required htmlFor="mrp" className='form-label'>
                      MRP
                    </InputLabel>
                    <TextField
                      fullWidth
                      size="small"
                      type="number"
                      name="mrp"
                      // label="Name"
                      variant="outlined"
                      onBlur={handleBlur}
                      value={values.mrp}
                      // onChange={handleChange}
                      onChange={(e) => {
                        handleChange(e)
                        const min_qty_net_rate = parseFloat(calculateNetRate(e, values));
                        var mrp = parseInt(values.mrp);
                        var min_qty_margin_percentage = (((mrp - min_qty_net_rate) * mrp) / 100);
                        setFieldValue("min_qty_net_rate", min_qty_net_rate.toFixed(2));
                        setFieldValue("min_qty_margin_percentage", min_qty_margin_percentage.toFixed(2));
                      }}
                      helperText={touched.mrp && errors.mrp}
                      error={Boolean(errors.mrp && touched.mrp)}
                      sx={{ mb: 3 }}
                    />
                  </Grid>
                  <Grid item sm={6} xs={12}>
                    <label htmlFor="product_image" className='form-label'>Upload Image</label>
                    <FormControl fullWidth={true} sx={{ mb: 3 }}>
                      <input
                        id="upload-photo"
                        name="product_image"
                        type="file"
                        onChange={changeHandler}
                      />
                    </FormControl>
                    {(errors.product_image)
                      && (touched.product_image)
                      && <div style={{ color: '#ff3d57' }}>{`${errors.product_image}`}</div>}

                  </Grid>
                </Grid>
                <Grid container style={{ padding: '20px 0px' }} spacing={2}>
                  <Grid item sm={6} xs={12} lg={2}>
                    <InputLabel required htmlFor="min_qty" className='form-label'>
                      Quantity
                    </InputLabel>
                    <TextField
                      fullWidth
                      size="small"
                      type="number"
                      name="min_qty"
                      // label="Name"
                      variant="outlined"
                      onBlur={handleBlur}
                      value={values.min_qty}
                      // onChange={handleChange}
                      onChange={(e) => {
                        handleChange(e)
                        const min_qty_net_rate = parseFloat(calculateNetRate(e, values));
                        var mrp = parseInt(values.mrp);
                        var min_qty_margin_percentage = (((mrp - min_qty_net_rate) * mrp) / 100);
                        setFieldValue("min_qty_net_rate", min_qty_net_rate.toFixed(2));
                        setFieldValue("min_qty_margin_percentage", min_qty_margin_percentage.toFixed(2));
                      }}
                      helperText={touched.min_qty && errors.min_qty}
                      error={Boolean(errors.min_qty && touched.min_qty)}
                      sx={{ mb: 3 }}
                    />
                  </Grid>
                  <Grid item sm={6} xs={12} lg={2}>
                    <InputLabel required htmlFor="min_qty_discount" className='form-label'>
                      Discount (%)
                    </InputLabel>
                    <TextField
                      fullWidth
                      size="small"
                      type="number"
                      name="min_qty_discount"
                      // label="Name"
                      variant="outlined"
                      onBlur={handleBlur}
                      value={values.min_qty_discount}
                      // onChange={handleChange}
                      onChange={(e) => {
                        handleChange(e)
                        const min_qty_net_rate = parseFloat(calculateNetRate(e, values));
                        var mrp = parseInt(values.mrp);
                        var min_qty_margin_percentage = (((mrp - min_qty_net_rate) * mrp) / 100);
                        setFieldValue("min_qty_net_rate", min_qty_net_rate.toFixed(2));
                        setFieldValue("min_qty_margin_percentage", min_qty_margin_percentage.toFixed(2));
                      }}
                      helperText={touched.min_qty_discount && errors.min_qty_discount}
                      error={Boolean(errors.min_qty_discount && touched.min_qty_discount)}
                      sx={{ mb: 3 }}
                    />
                  </Grid>
                  <Grid item sm={6} xs={12} lg={2}>
                    <InputLabel required htmlFor="min_qty_bonus_deal" className='form-label'>
                      Bonus (Deal)
                    </InputLabel>
                    <TextField
                      fullWidth
                      size="small"
                      type="text"
                      name="min_qty_bonus_deal"
                      variant="outlined"
                      onBlur={handleBlur}
                      value={values.min_qty_bonus_deal}
                      // onChange={handleChange}
                      onChange={(e) => {
                        handleChange(e)
                        const min_qty_net_rate = parseFloat(calculateNetRate(e, values));
                        var mrp = parseInt(values.mrp);
                        var min_qty_margin_percentage = (((mrp - min_qty_net_rate) * mrp) / 100);
                        setFieldValue("min_qty_net_rate", min_qty_net_rate.toFixed(2));
                        setFieldValue("min_qty_margin_percentage", min_qty_margin_percentage.toFixed(2));
                      }}
                      helperText={touched.min_qty_bonus_deal && errors.min_qty_bonus_deal}
                      error={Boolean(errors.min_qty_bonus_deal && touched.min_qty_bonus_deal)}
                      sx={{ mb: 3 }}
                    />
                  </Grid>
                  <Grid item sm={6} xs={12} lg={2}>
                    <InputLabel required htmlFor="min_qty_trade_rate" className='form-label'>
                      Trade Rate
                    </InputLabel>
                    <TextField
                      fullWidth
                      size="small"
                      type="number"
                      name="min_qty_trade_rate"
                      variant="outlined"
                      onBlur={handleBlur}
                      value={values.min_qty_trade_rate}
                      // onChange={handleChange}
                      onChange={(e) => {
                        handleChange(e)
                        const min_qty_net_rate = parseFloat(calculateNetRate(e, values));
                        var mrp = parseInt(values.mrp);
                        var min_qty_margin_percentage = (((mrp - min_qty_net_rate) * mrp) / 100);
                        setFieldValue("min_qty_net_rate", min_qty_net_rate.toFixed(2));
                        setFieldValue("min_qty_margin_percentage", min_qty_margin_percentage.toFixed(2));
                      }}
                      helperText={touched.min_qty_trade_rate && errors.min_qty_trade_rate}
                      error={Boolean(errors.min_qty_trade_rate && touched.min_qty_trade_rate)}
                      sx={{ mb: 3 }}
                    />
                  </Grid>
                  <Grid item sm={6} xs={12} lg={2}>
                    <label htmlFor="min_qty_net_rate" className='form-label'>Net Rate</label>
                    <TextField
                      fullWidth
                      disabled
                      size="small"
                      type="text"
                      name="min_qty_net_rate"
                      variant="outlined"
                      onBlur={handleBlur}
                      value={values.min_qty_net_rate}
                      onChange={handleChange}
                      // helperText={touched.min_qty_net_rate && errors.min_qty_net_rate}
                      // error={Boolean(errors.min_qty_net_rate && touched.min_qty_net_rate)}
                      sx={{ mb: 3 }}
                    />
                  </Grid>
                  <Grid item sm={6} lg={2} xs={12}>
                    <label htmlFor="min_qty_margin_percentage" className='form-label'>Margin%</label>
                    <TextField
                      fullWidth
                      disabled
                      size="small"
                      type="text"
                      name="min_qty_margin_percentage"
                      variant="outlined"
                      onBlur={handleBlur}
                      value={values.min_qty_margin_percentage}
                      onChange={handleChange}
                      // helperText={touched.min_qty_margin_percentage && errors.min_qty_margin_percentage}
                      // error={Boolean(errors.min_qty_margin_percentage && touched.min_qty_margin_percentage)}
                      sx={{ mb: 3 }}
                    />
                  </Grid>
                </Grid>

                <h4 style={{ color: theme.palette.primary.main, fontSize: '18px', }}>Tax Category</h4>
                <Grid container style={{ padding: '20px 0px' }} spacing={2}>
                  <Grid item sm={12} xs={12} lg={8}>
                    <Box overflow="auto">
                      <TaxCategoryTable>
                        <TableHead>
                          <TableRow>
                            <TableCell></TableCell>
                            <TableCell>CGST</TableCell>
                            <TableCell>SGST</TableCell>
                            <TableCell>IGST</TableCell>
                            <TableCell>CESS</TableCell>
                          </TableRow>
                        </TableHead>
                        <TableBody>
                          {tax_categories && tax_categories.map((tax, index) => (
                            <TableRow key={index}>
                              <TableCell>
                                <Radio
                                  checked={values.tax_category_id == tax.id}
                                  // onChange={handleChange}
                                  onChange={(e) => {
                                    handleChange(e)
                                    const min_qty_net_rate = parseFloat(calculateNetRate(e, values));
                                    var mrp = parseInt(values.mrp);
                                    var min_qty_margin_percentage = (((mrp - min_qty_net_rate) * mrp) / 100);
                                    setFieldValue("min_qty_net_rate", min_qty_net_rate.toFixed(2));
                                    setFieldValue("min_qty_margin_percentage", min_qty_margin_percentage.toFixed(2));
                                  }}
                                  value={tax.id}
                                  name="tax_category_id"
                                  inputProps={{ 'aria-label': 'A' }}
                                />
                              </TableCell>
                              <TableCell>{tax.cgst}</TableCell>
                              <TableCell>{tax.sgst}</TableCell>
                              <TableCell>{tax.igst}</TableCell>
                              <TableCell>{tax.cess}</TableCell>
                            </TableRow>
                          ))}
                        </TableBody>
                      </TaxCategoryTable>
                      {(errors.tax_category_id)
                        && (touched.tax_category_id)
                        && <div style={{ color: '#ff3d57' }}>{`${errors.tax_category_id}`}</div>}
                    </Box>
                  </Grid>
                </Grid>

                <Grid container style={{ padding: '20px 0px' }} spacing={2}>
                  <Grid item sm={6} xs={12} lg={2}>
                    <LoadingButton
                      fullWidth
                      type="submit"
                      color="primary"
                      loading={loading}
                      variant="contained"
                      sx={{ mb: 2, mt: 3 }}
                    >
                      Save
                    </LoadingButton>
                  </Grid>
                </Grid>
              </form>
            )}
          </Formik>
        </CardContent>
      </Card>
    </Container >
  );
};

export default AddMedicinesStock;
